*,
::before,
::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}
@media screen and (max-width: 576px) {
  html {
    font-size: 8px;
  }
}
@media screen and (max-width: 768px) {
  html {
    font-size: 9px;
  }
}
@media screen and (min-width: 1600px) {
  html {
    font-size: 11px;
  }
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

body {
  font-size: 1.6rem;
  background-color: #f1f5fa;
  min-height: 100vh;
  color: #001632;
  overflow-x: hidden;
}

.flex {
  display: flex;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-align {
  align-items: center;
}

.row-reverse {
  flex-direction: row-reverse;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-column-align {
  justify-content: center;
  align-items: center;
}

.wrap {
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.w-50 {
  width: 50%;
}

.grid-33 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
@media screen and (max-width: 576px) {
  .grid-33 {
    grid-template-columns: 1fr;
  }
}

.grid-25 {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(4, 1fr);
}
@media screen and (max-width: 992px) {
  .grid-25 {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (max-width: 576px) {
  .grid-25 {
    gap: 3rem;
    grid-template-columns: 1fr;
  }
}

.grid-50 {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  height: 100%;
}
@media screen and (max-width: 992px) {
  .grid-50 {
    gap: 2rem;
    grid-template-columns: 1fr;
  }
}

.container {
  max-width: 94rem;
  margin: 0 auto;
}

section {
  padding: 0 1.6rem;
}

header {
  background-color: #fff;
  padding: 1.6rem 8rem;
}
@media screen and (max-width: 992px) {
  header {
    padding: 1.6rem;
  }
}
header .nav .container {
  display: flex;
  flex-direction: direction;
  justify-content: justify;
  align-items: align;
}
header .nav-logo {
  width: 13.3rem;
}

